<template>
  <div class="container">
    <bar-chart
      id="total-cost" :data="chartData" xkey="region" ykeys='[ "flight", "hotel", "food" ]'
      resize="true"
      labels='[ "비행기", "숙소", "음식" ]' bar-colors='[ "#FF6384", "#36A2EB", "#FFCE56" ]'
      grid="true"
      grid-text-size="7"
    >
      <!--grid-text-weight="bold"-->
    </bar-chart>
  </div>
</template>

<script>
  import { BarChart } from 'vue-morris'

  export default  {
    name: 'TotalCost',

    data() {
      return {
        chartData: [
          { region: '후쿠오카', flight: 149700, hotel: 179482, food: 30000 },
          { region: '오키나와', flight: 224050, hotel: 200737, food: 30000 },
          { region: '오사카', flight: 155300, hotel: 156033, food: 30000 },
          { region: '방콕', flight: 445000, hotel: 75196, food: 30000 }
        ],
      }
    },

    components: {
      BarChart
    }
  }
</script>
<style scoped>
  .container {

  }
</style>




