// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import Raphael from 'raphael/raphael';
global.Raphael = Raphael;
import 'chart.js';
import 'hchs-vue-charts';
import SuiVue from 'semantic-ui-vue';
import 'semantic-ui-css/semantic.min.css';
import router from './router';
import { store } from './Vuex/store';
import AnimatedVue from 'animated-vue';
import 'animate.css/animate.css';
import vueg from 'vueg';
import 'vueg/css/transition-min.css';
import 'fullpage-vue/src/fullpage.css';
import VueFullpage from 'fullpage-vue';



var VueTouch = require('vue-touch');
const options={
  duration: '0.5',              // `animation-duration`
  firstEntryDisable: true,     // When the value is true, in the process of the user into the app, app no transition effect,the default is false
  firstEntryDuration: '.6',     //
  forwardAnim: 'fadeInUp',   // Forward transition effect,the default is fadeInRight
  backAnim: 'fadeInDown',       // Back transition effect,the default is fadeInLeft
  sameDepthDisable: false,      // When the url depth is the same, the effect is disabled,the default is false
  tabs: [],                       // the default is [],'name' is the name of the route,to achieve in the app click on the tab,the page horizontal transition effects,such as tabs[1] to tab [0],will use the backAnim effect,tabs[1] to tabs[2],will use the forwardAnim effect
  tabsDisable: false,           // when the value is true,disable the effect of the page in tabs,the default is false
  disable: false,               // disable transition effect,the default is false,nested route default is true
  nuxt: false                   // If you use the back-end rendering framework Nuxt, set this to true
}

Vue.use(VueTouch, {name: 'v-touch'});
Vue.config.productionTip = false;
Vue.use(SuiVue);
Vue.use(window.VueCharts);
Vue.use(AnimatedVue);
// Vue.use(vueg, router, options);
Vue.use(VueFullpage);

/* eslint-disable no-new */


new Vue({
  el: '#app',
  components: { App },
  store,
  router,
  template: '<App/>'
})



