<template>
    <div class="container page page-2">
      <img src="http://drive.google.com/uc?export=view&id=1ITJXeaXUG_2mFJ1zSzN3Qh9bZ3GTMUhx">
      <div class="header">
        <span>항공권</span>
        <hr>
        <p class="description">
          가는날 - 오전 7:30<br>
          오는날 - 오후 4:00
        </p>
        <div class="clearfix"></div>
      </div>
      <div class="article">
        <span class="article-header">항공권을 비교 해봐요!</span>
        <p class="article-description">
          이번에 계획된 여행은 7월 중순 이군요.<br>
          7월 말 부터 8월 중순 까지 이어지는<br>
          직장인 휴가철을 잘 피했네요!<br>
          저렴한 가격에 항공권을 구입할 수 있겠어요!<br>
          으음................<br>
          유독 길어보이는 막대가 있네요?
        </p>
      </div>
      <div class="chart">
        <flight-price/>
      </div>
    </div>
</template>

<script>
  import FlightPrice from '../charts/FlightPrice';

  export default {
    name: "FlightPriceScreen",

    data() {
      return {

      }
    },

    methods: {
      navigationToHotel() {
        this.$router.push({path: '/exchange/flight/hotel'});
      },

      navigationToExchange() {
        this.$router.push({path: '/exchange'})
      }
    },

    components: {
      FlightPrice
    }
  }
</script>

<style scoped>
  .container {
    background: white;
    /*height: -webkit-fill-available;*/
    /*position: relative;*/
  }

  img {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%;
    /*border-radius: 15px;*/
    z-index: 1;
    filter: blur(3.5px);
    opacity: 0.4;
  }

  .header {
    padding-left: 1rem;
    padding-top: 3.5rem;
    padding-bottom: 3rem;
  }

  .header>span {
    font-size: 3rem;
    color: #979797;
  }

  hr {
    border: 1px solid #b7b7b7;
    width: 8rem;
  }

  .description {
    font-size: 1rem;
  }

  .article {
    background: #2c2c2c;
    background: rgba(0, 0, 0, 0.5);
    color: #666666;
    position: absolute;
    width: 100%;
    z-index: 1;
    color: white;
    border-radius: 15px;
    padding-top: 3.5rem;
    padding-left: 1rem;
    padding-bottom: 3rem;
    opacity: 0.7;
  }

  .article-header {
    font-size: 2.5rem;
  }

  .article-description {
    padding-top: 1rem;
    font-size: 1.5rem;
  }

  .clearfix {
    content: "";
    clear: both;
  }

  .chart {
    position: relative;
    top: 40%;
  }
</style>



