<template>
  <div class="container page page-4">
    <img src="http://drive.google.com/uc?export=view&id=13ybCsUuDNN_zvUIUEBHTZ_iTkF408dF5">
    <div class="header">
      <span class="title">호텔</span>
      <hr>
      <p class="subtitle">당신의 여행을 편안하게</p>
    </div>
    <div class="article">
      <span class="article-header">호텔 가격은 어떨까요?</span>
      <p class="article-description">
        각 지역마다 낮은 가격과 높은가격, 평균가격을<br>
        찾아봤어요.<br>
        한 군데를 제외한 나머지 지역들은 모두 비슷하군요?<br>
        비행기 가격과는 완전 딴판이네요!<br>
        하지만, 우리의 머릿수는 5개라는 사실을<br>
        간과하시면 안됩니다!<br>
        (사실 항공권 가격은 1명이 부담하는 거지만<br>
        숙소비용은 엔빵이잖아요?!
      </p>
    </div>
    <div class="chart">
      <hotel-price/>
    </div>
  </div>
</template>

<script>
  import HotelPrice from '../charts/HotelPrice'

  export default {
    name: "HotelPriceScreen",

    data() {
      return {

      }
    },

    methods: {
      navigationToTotalCost() {
        this.$router.push({path: '/exchange/flight/hotel/total'});
      },

      navigationToFlight() {
        this.$router.push({path: '/exchange/flight'})
      }
    },

    components: {
      HotelPrice
    }
  }
</script>

<style scoped>
  .container {
    /*height: -webkit-fill-available;*/
    /*position: relative;*/
  }

  img {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 41rem;
    /*border-radius: 15px;*/
    z-index: 1;
    filter: blur(3.5px);
    opacity: 0.3;
  }

  .header {
    padding-left: 1rem;
    padding-top: 3.5rem;
    padding-bottom: 5rem;
    color: #979797;
    /*background: blue;*/
  }

  .title {
    font-size: 3rem;
  }

  .subtitle {
    padding-top: 0.3rem;
    font-size: 1.2rem;
  }

  hr {
    margin-top: 5px;
    border: 1px solid #b7b7b7;
    width: 11rem;
  }

  .article {
    position: absolute;
    z-index: 2;
    opacity: 0.8;
    color: #656565;
    padding-top: 3.5rem;
    padding-left: 1rem;
    /*background: red;*/
    /*padding-bottom: 8rem;*/
  }

  .article-header {
    font-size: 2.5rem;
  }

  .article-description {
    padding-top: 1rem;
    font-size: 1.5rem;
  }

  .chart {
    /*background: green;*/
    /*padding-top: 90%;*/
    position: relative;
    top: 50%;
  }




</style>



