<template>
  <div class="container">
    <div class="wrapper">
    <div class="lowest-price">
      <p class="description">낮은 가격</p>
      <div class="chart">
        <donut-chart
          id="lowest"
          :data="lowest"
          labels="['test1', 'test2', 'test3']"
          colors='["#FF6384", "#36A2EB", "#FFCE56", "#60ffaa"]'
          resize="true"
        />
      </div>
    </div>
    <div class="higher-price">
      <p class="description">높은 가격</p>
      <div class="chart">
        <donut-chart
          id="higher"
          :data="higher"
          colors='["#FF6384", "#36A2EB", "#FFCE56", "#60ffaa"]'
          resize="true"
        />
      </div>
    </div>
    <div class="average-price">
      <p class="description">평균 가격</p>
      <div class="chart">
        <donut-chart
          id="average"
          :data="average"
          colors='["#FF6384", "#36A2EB", "#FFCE56", "#60ffaa"]'
          resize="true"
        />
      </div>
    </div>
    <div class="clearfix"></div>
    </div>
  </div>
</template>

<script>
  import {DonutChart} from 'vue-morris'

  export default {
    name: "HotelPrice",
    components: {DonutChart},
    data() {
      const Fukuoka = this.$store.state.hotelPrice[0];
      const Okinawa = this.$store.state.hotelPrice[1];
      const Osaka = this.$store.state.hotelPrice[2];
      const Bangkok = this.$store.state.hotelPrice[3];

      return {
        lowest: [
          {label: '후쿠오카', value: Fukuoka.lowest},
          {label: '오키나와', value: Okinawa.lowest},
          {label: '오사카', value: Osaka.lowest},
          {label: '방콕', value: Bangkok.lowest},
        ],

        higher: [
          {label: '후쿠오카', value: Fukuoka.higher},
          {label: '오키나와', value: Okinawa.higher},
          {label: '오사카', value: Osaka.higher},
          {label: '방콕', value: Bangkok.higher},
        ],

        average: [
          {label: '후쿠오카', value: Fukuoka.average},
          {label: '오키나와', value: Okinawa.average},
          {label: '오사카', value: Osaka.average},
          {label: '방콕', value: Bangkok.average},
        ],
      }
    },

    comments: {
      DonutChart,
    }
  }
</script>

<style scoped>
  .container {
    position: relative;
    overflow: hidden;
  }
  .wrapper {
    display: flex;
    justify-content: center;
    overflow: hidden;
  }

  .lowest-price, .higher-price, .average-price {
    width: 100%;
    margin-left: 10px;
  }

  svg {
    /*height: 200px !important;*/
  }

  .chart {
    margin-top: -85%;
  }

  .clearfix {
    /*content: "";*/
    /*clear: both;*/
    /*overflow: hidden;*/
  }

  .description {
    font-size: 1.2rem;
    color: #aeaeae;
    font-weight: 500;
    margin-bottom: -1rem;
  }
</style>



