import Vue from 'vue';
import Router from 'vue-router'

import ExchangeRateScreen from '../components/screens/ExchangeRateScreen';
import FlightPriceScreen from '../components/screens/FlightPriceScreen';
import HotelPriceScreen from '../components/screens/HotelPriceScreen';
import TotalCostScreen from '../components/screens/TotalCostScreen';
import IntroScreen from '../components/screens/IntroScreen';
import RegionSelectScreen from '../components/screens/RegionSelectScreen';
import MainPage from '../components/pages/MainPage';

Vue.use(Router);

export default new Router ({
  routes: [
    {
      path: '/',
      name: 'intro',
      component: MainPage
    },
    {
      path: '/exchange',
      name: 'test1',
      component: ExchangeRateScreen
    },
    {
      path: '/exchange/flight',
      name: 'test2',
      component: FlightPriceScreen
    },
    {
      path: '/exchange/flight/hotel',
      name: 'test3',
      component: HotelPriceScreen
    },
    {
      path: '/exchange/flight/hotel/total',
      name: 'test4',
      component: TotalCostScreen
    },
    {
      path: '/exchange/flight/hotel/total/select',
      name: 'region-select',
      component: RegionSelectScreen
    },
    {
      path: '*',
      redirect: '/',
    },

  ]
})



