<template>
  <div class="container fullpage-container">
    <div class="fullpage-wp" v-fullpage="options" ref="fullpage">
      <!--<div class="page-1 page">-->
        <!--<div class="part-1" v-animate="{value: 'fadeInDown'}">-->
          <IntroScreen/>
        <!--</div>-->
      <!--</div>-->
      <!--<div class="page-2 page">-->
        <flight-price-screen/>
      <!--</div>-->
      <!--<div class="page-3 page">-->
        <ExchangeRateScreen/>
      <!--</div>-->
      <!--<div class="page-4 page">-->
        <hotel-price-screen/>
      <!--</div>-->
      <!--<div class="page-5 page">-->
        <total-cost-screen/>
      <!--</div>-->
      <!--<div class="page-6 page">-->
        <region-select-screen/>
      <!--</div>-->
    </div>
  </div>
</template>

<script>
  import IntroScreen from '../screens/IntroScreen'
  import FlightPriceScreen from '../screens/FlightPriceScreen'
  import ExchangeRateScreen from '../screens/ExchangeRateScreen'
  import HotelPriceScreen from '../screens/HotelPriceScreen'
  import TotalCostScreen from '../screens/TotalCostScreen'
  import RegionSelectScreen from '../screens/RegionSelectScreen'

  export default {
    name: "MainPage",

    components: {
      IntroScreen,
      ExchangeRateScreen,
      FlightPriceScreen,
      HotelPriceScreen,
      TotalCostScreen,
      RegionSelectScreen
    },

  data() {
      const self = this;
      return {
        index: 0,
        pageNum: 0,
        options: {
          start: 0,
          dir: 'v',
          loop: false,
          duration: 300,
          beforeChange: function(ele, current, next) {
            self.index = next;
          },
          afterChange: function(ele, current) {
            self.index = current;
          }
        }
      };
    }
  }
</script>

<style scoped>
  .container {
    margin: 0 auto !important;
    max-width: 500px;
  }

</style>



