<template>
  <div class="container">
    <chartjs-horizontal-bar
    :datalabel="'항공권 가격'"
    :labels="this.labels"
    :bodyFontColor="'#ffffff'"
    :data="this.priceData"
    :backgroundcolor="this.chartColor"
    :bordercolor="this.borderColor"
    :option="options"
    >
    </chartjs-horizontal-bar>
  </div>
</template>

<script>

  export default  {
    name: 'FlightPrice',
    data() {
      return {
        labels: this.$store.state.flightPrice.map(item => item.country),
        priceData: this.$store.state.flightPrice.map(country => country.price),

        chartColor: [
          '#FF6384', '#36A2EB', '#FFCE56', '#4eff7b'
        ],

        borderColor: [
          '#FF6384', '#36A2EB', '#FFCE56', '#4eff7b'
        ],

        options: {
          legend: {
            labels: {
              fontColor: '#a5a5a5'
            }
          },
          scales: {
            xAxes: [{
              gridLines: {
                offsetGridLines: false
              }
            }]
          }
        }
      }
    },
  }
</script>

<style scoped>
  .container {
    background: white;
  }
</style>




