<template>
    <div class="container">
      <line-chart
        id="line"
        :data="exchangeRate"
        ymin="969"
        ymax="1038"
        xkey="month"
        ykeys='["JPY", "THB"]'
        line-colors='["#FF6384", "#36A2EB"]'
        labels='["엔", "바트"]'
        gird="true"
        resize="true"
      >
      </line-chart>
    </div>
</template>

<script>
  import {LineChart} from 'vue-morris';


  export default {
    name: "Exchange",

    data() {
      return {
        exchangeRate: this.$store.state.exchangeRate

      }
    },

    components: {
      LineChart,
    }
  }
</script>

<style scoped>
  .container {
    resize: horizontal;
  }
</style>



