<template>
    <div class="container page page-5">
      <div class="header">
        <span>한 눈에 보기</span>
        <hr>
        <p class="subtitle">모든 지역 비교</p>
      </div>
      <div class="article">
        <span class="article-header">다 보기 귀찮아서 모아봤어요.</span>
        <p class="article-description">
          종합적인 비용을 산출해 봤을때<br>
          '방콕'이 가장 비싸군요.<br>
          음.. 아무래도 다른 곳에 비해 무려 20만원이나<br>
          비싼 항공권이 문제인것 같군요!<br>
          하지만 단점이 있다면 장점도 있는법이죠.<br>
          일단 가기만 하면 모든것이 싸다니깐요?
        </p>
      </div>
      <div class="chart">
        <total-cost/>
      </div>
    </div>
</template>

<script>
  import TotalCost from '../charts/TotalCost'

  export default {
    name: "TotalCostScreen",

    data() {
      return {

      }
    },

    methods: {
      navigationToHotel() {
        this.$router.push({path: '/exchange/flight/hotel/'});
      },

      navigationToRegionSelect() {
        this.$router.push({path: '/exchange/flight/hotel/total/select'});
      },
    },

    components: {
      TotalCost
    }
  }
</script>

<style scoped>
  .container {
    /*height: -webkit-fill-available;*/
    /*position: relative;*/
    /*padding-top: 2.5rem;*/
  }

  .header {
    color: #979797;
    padding-left: 1rem;
    padding-bottom: 1rem;
    padding-top: 1rem;
  }

  .subtitle {
    padding-top: 0.3rem;
    font-size: 1.2rem;
  }

  .header>span {
    font-size: 3rem;
  }

  hr {
    margin-top: 5px;
    border: 1px solid #b7b7b7;
    width: 15rem;
  }

  .article {
    color: #666666;
    position: absolute;
    padding-top: 3.5rem;
    padding-left: 1rem;
  }

  .article-header {
    font-size: 2.5rem;
  }

  .article-description {
    padding-top: 1rem;
    font-size: 1.5rem;
  }

  .chart {
    position: relative;
    top: 27%;
  }

</style>



